/* src/main/frontend/src/components/NavBar.css */

/* 네비게이션 바의 기본 스타일 설정: 배경색, 내부 여백 */
.navbar {
    background-color: #f1f1f133; /* 진한 회색 배경 */
    padding: 1rem; /* 모든 면에 1rem의 여백 */
    height: 100vh; /* 화면 전체 높이 */
    width: 200px; /* 원하는 너비 설정 */
    position: fixed; /* 화면에 고정 */
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column; /* 수직 배치 */
    align-items: center;
}

/* 네비게이션 바 내부의 목록(<ul>) 스타일: 리스트 스타일 제거, 가운데 정렬을 위한 flexbox 설정 */
.navbar-list {
    list-style-type: none; /* 리스트 불릿 제거 */
    margin: 0; /* 외부 여백 제거 */
    padding: 0; /* 내부 여백 제거 */
    display: flex; /* 플렉스박스 모델 적용 */
    flex-direction: column; /* 수직 배치 */
    width: 100%;
}

/* 각 네비게이션 항목(<li>)의 스타일: 인라인으로 표시 */
.navbar-item {
    width: 100%;
}

/* 네비게이션 링크의 기본 스타일: 색상, 텍스트 꾸밈 제거, 여백 */
.navbar-link {
    color: white; /* 텍스트 색상을 흰색으로 설정 */
    text-decoration: none; /* 밑줄 없애기 */
    padding: 0.5rem 1rem; /* 상하 0.5rem, 좌우 1rem의 패딩 */
    display: block;
    text-align: center;
}

/* 네비게이션 링크에 마우스 오버 시 스타일 변경: 배경 색상, 모서리 둥글게 */
.navbar-link:hover {
    background-color: #555; /* 마우스 오버 시 진한 회색 배경 */
    border-radius: 4px; /* 모서리 둥글게 처리 */
}
